.work-container {
    margin: 0 3%;
}

.work-gallery {
    margin: -1.5vw -1.5vw CALC( -1 * ( 1.5vw + 1.5em + 5px ) ) -1.5vw;
}

.work {
   z-index: auto;
   width: CALC( 100% / 3 - 2 * 1.5vw );
   margin: 1.5vw 1.5vw CALC( 1.5vw + 3rem ) 1.5vw;
   position: relative;

   box-sizing: border-box;
   text-decoration: inherit;
   color: inherit;

   @media (max-width: 800px) {
      width: CALC( 100% / 2 - 2 * 1.5vw );
   }

   @media (max-width: 500px) {
      width: 100%;
   }
}

.work-image {
   width: 85%;
   background-color: var(--white);
   height: auto;
   padding: 2rem;
   box-sizing: border-box;
   border: 2px solid black;
}

.work-image--absolute {
   position: absolute;
   z-index: 200;
   top: 0;
   left: 0;
}

.work-text {    
    z-index: 100;
    width: 85%;
    box-sizing: border-box;
    background-color: var(--red);
    border: 2px solid var(--white);
    padding: 1rem;
    position: absolute;
    bottom: -3rem;
    left: 15%;
    max-height: 100%;

    &:hover {
        z-index: 300;
    }
}

.work-text-description {
    margin-bottom: 1.5rem;
    opacity:0;
    transition:opacity 0.5s linear;
    font-size: 0.8rem;

    @nest .work-text:hover & {
        opacity: 1;
    }
}

.work-text-headline {
    text-transform: uppercase;
}